import Color from "color"; // "@setCustomTheme" 是使用webpack的别名定义的

import setCustomTheme from "@setCustomTheme";
import Forms from "./components/forms.vue";
import Menu from "./components/menu.vue";
import Message from "./components/message.vue";
export default {
  name: "App",
  components: {
    Forms: Forms,
    Menu: Menu,
    Message: Message
  },
  data: function data() {
    return {
      primaryColor: "#512da7"
    };
  },
  methods: {
    colorChange: function colorChange(val) {
      setCustomTheme({
        Color: Color,
        primaryColor: val // npm run serve 之后，可以使用终端命令 npx z-theme inspect 查看 gradientReplacer的可用属性
        // gradientReplacer: { "#F7D06B":"#F7D06B" },

      });
    }
  }
};